<template>
    <!-- info feature begin-->
    <div class="joinbox">
      <div class="container">
        <div class="row">
          <div class="col-sm-12 col-md-8 offset-md-2">
            <ul class="nav nav-tabs jointab" role="tablist">
              <li role="presentation">
                <a href="javascript:void(0)" v-on:click="tab(1)" v-bind:class="{'active': tabNo==1}">{{ $t("joinbox.tab01") }}</a>
              </li>
              <li role="presentation">
                <a href="javascript:void(0)" v-on:click="tab(2)" v-bind:class="{'active': tabNo==2}">{{ $t("joinbox.tab02") }}</a>
              </li>
              <li role="presentation">
                <a href="javascript:void(0)" v-on:click="tab(3)" v-bind:class="{'active': tabNo==3}">{{ $t("joinbox.tab03") }}</a>
              </li>
            </ul>
            <div class="tab-content">
              <div role="tabpanel" class="tab-pane active" id="tab01">
                <div class="row rulelist">
                  <div class="rulelistbox col-sm-12">
                    <ul class="list-inline" >
                      <li class="list-inline row" v-for="(item,index) in tabList" v-bind:key='index'>
                        <div class="col-7 text-left" >{{item.name}}</div>
                        <div class="col-5 text-right"><span>{{dataDataList[index]}}</span></div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
                <div class="row"><br>
                    <div class="col-md-6 offset-md-3">
                        <button type="button" class="btn btn-outline-primary btn-block withdrawBtn" v-on:click="cleanBonus" >{{ $t("joinbox.clean") }}</button>
                    </div>
                </div>
              <div class="row"><br>
                <div class="col-md-6 offset-md-3">
                    <button type="button" class="btn btn-outline-primary btn-block withdrawBtn" v-on:click="cash" >{{ $t("joinbox.cash") }}</button>
                </div>
              </div>
              <div class="row"><br>
                <div class="col-md-6 offset-md-3">
                  <button type="button" class="btn btn-outline-primary btn-block withdrawBtn" v-clipboard:copyhttplist="httpUrl" v-clipboard:success="onCopy">{{ $t("joinbox.recommend") }}</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
        <!-- preloader begin-->
        <div class="preloader" v-bind:style="{ display: preShow }">
        <div class="loader"><hr><hr></div>
        </div>
        <!-- preloader end -->
    </div>
</template>

<script>
import {NETWORKS} from '../util/constants/networks'
import {mapState} from 'vuex'
import { connect } from "tls";
import { fail } from 'assert';

export default {
    props: ['message'],
    name: "miracle-data",
    computed: {
        ...mapState({
            isInjected: state => state.web3.isInjected,
            networkId: state => state.web3.networkId,
            network: state => NETWORKS[state.web3.networkId],
            coinbase: state => state.web3.coinbase,
            balance: state => state.web3.balance,
            httpUrl: state => 'https://'+document.domain+'?code='+state.web3.coinbase,
            ethBalance: state => {
            if (state.web3.web3Instance !== null) return state.web3.web3Instance().fromWei(state.web3.balance, 'ether')
            }    
        }),
        shouyiList() {
            return [
                {'name':this.$t("joinbox.Onelist01")},
                {'name':this.$t("joinbox.Onelist02")},
                {'name':this.$t("joinbox.Onelist05")},
                {'name':this.$t("joinbox.Onelist04")},
                {'name':this.$t("joinbox.Onelist03")},
                {'name':this.$t("joinbox.Onelist06")},
                {'name':this.$t("joinbox.Onelist07")},
                {'name':this.$t("joinbox.Onelist08")}
            ]
        },
        yejiList() {
            return [
                {'name':this.$t("joinbox.Twolist01")},
                {'name':this.$t("joinbox.Twolist02")},
                {'name':this.$t("joinbox.Twolist03")}
            ]
        },
        dataList() {
            return [
                {'name':this.$t("joinbox.Threelist01")},
                // {'name':this.$t("joinbox.Threelist02")},
                {'name':'V1'},
                {'name':'V2'},
                {'name':'V3'},
                {'name':'V4'}
            ]
        },
        levels(){
            return ['--',this.$t("vip.vip1"),this.$t("vip.vip2"),this.$t("vip.vip3")]
        },
        dataDataList(){
            return this.tabData
        }
    },
    data() {
        return {
            preShow:'block',
            tokenAlert:false,
            tabList:{},
            tabNo:1,            
            VIPS:['--','V1','V2','V3'],
            myFee:0,
            burnRatio:0,
            shouyiData:[],
            yejiData:[],
            dataData:[],
            tabData:[]
        };
    },
    created: function() {
        this.tabList = this.shouyiList;
        this.tabData = [];
    },
    watch:{
        shouyiList(){
            if(this.tabNo==1){
                this.tabList = this.shouyiList;
            }
        },
        yejiList(){
            if(this.tabNo==2){
                this.tabList = this.yejiList;
            }
        },
        dataList(){
            if(this.tabNo==3){
                this.tabList = this.dataList;
            }
        },
        dataDataList(){
            console.log('Get User Ok');
        },
        network(){
            if(this.networkId==1){
                this.getUser();
                this.preShow='none';
            }else{
                this.$toastr.error(this.$t("msg.msg3"));
                // return false;
            }
            // console.log(this.network);
            // console.log(this.networkId);
        }
    },
    methods: {
        onCopy(e) {
            this.$toastr.success(this.$t("msg.copy"));
        },
        tab(data){
            if(data==1){
                this.tabNo = 1;
                this.tabList = this.shouyiList;
                this.tabData = this.shouyiData;
            }else if(data==2){
                this.tabNo = 2;
                this.tabList = this.yejiList;
                this.tabData = this.yejiData;
                
            }else{
                this.tabNo = 3;
                this.tabList = this.dataList;
                this.tabData = this.dataData;
            }
        },
        cleanBonus(event) {
            this.$store.state.contractInstance().everydayBonus({
                gas: 300000,
                value: this.$store.state.web3.web3Instance().toWei(this.amount, "ether"),
                from: this.$store.state.web3.coinbase
            },(err, result) => {
            if (err) {
                console.log(err);
            } else {
                let Won = this.$store.state.contractInstance().Won();
                    Won.watch((err, result) => {
                        if (err) {
                            console.log("could not get event Won()");
                        } else {
                            console.log(result);
                        }
                    });
                }
            });
        },
        getUser(event) {
            this.$store.state.contractInstance().getUser({ from: this.$store.state.web3.coinbase },
            (err, result) => {
                if (err) {
                    console.log(Number(err));
                } else {
                    this.shouyiData[0] = isNaN(Number(result[0]))?'--':this.levels[Number(result[0])];
                    this.shouyiData[1] = this.VIPS[Number(result[1])];
                    this.shouyiData[2] = this.fromWei(result[2]) +' ETH';
                    this.shouyiData[3] = this.fromWei(result[3]) +' ETH';
                    this.shouyiData[4] = this.fromWei(result[4]) +' ETH';
                    this.shouyiData[5] = this.fromWei(result[5]) +' ETH';
                    this.shouyiData[6] = this.fromWei(result[6]) +' ETH'; // Fee
                    this.shouyiData[7] = this.fromWei(result[7]) +' ETH'; //TouziGangGanFee
                    this.myFee = result[6];
                    this.tabData = this.shouyiData;                    
                }
            });

            this.$store.state.contractInstance().getUserData(
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                        console.log(err);
                    } else {
                        this.yejiData[0] = Number(result[0]);
                        this.yejiData[1] = this.fromWei(result[1]) +' ETH';
                        this.yejiData[2] = this.fromWei(result[2]) +' ETH';
                    }
                }
            );
            this.$store.state.contractInstance().getGuDong(
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                        console.log(err);
                    } else {
                        for(var i=0;i<=4;i++){
                            this.dataData[i] = Number(result[i]);
                        }
                    }
                }
            );
        },
        fromWei(data){
            return data>0?this.$store.state.web3.web3Instance().fromWei(data,'ether'):0;
        },
        /* getLevels(event) {
            this.$store.state.contractInstance().getLevels(1,
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                        console.log(err);
                    } else {
                        this.number = result;
                    }
                }
            );
        }, */
        cash(){
            if(this.myFee<=0){
                this.$toastr.warning(this.$t("msg.mag2"));
                return false;
            }
            this.$store.state.contractInstance().cash({
                gas: 300000,
                value: '0',
                from: this.$store.state.web3.coinbase
            },(err, result) => {
                if (err) {
                    console.log(err);
                    this.pending = false;
                } else {
                    let Won = this.$store.state.contractInstance().Won();
                    Won.watch((err, result) => {
                        if (err) {
                            console.log("could not get event Won()");
                        } else {
                            console.log(result);
                        }
                    });
                }
            });
        }
    },
    mounted() {
        this.$store.dispatch("getContractInstance");
        var _this = this;
        setInterval(function() {
            _this.getUser();
        }, 3000);
    }
};
</script>

<style scoped>
.loginCan {
  position: absolute;
  top: 100px;
  left: 0;
  width: 100%;
  box-sizing: border-box;
}
</style>




